import React from "react";
import useSeason from "../hooks/useSeason";

const seasonalThemes = {
  spring: "bg-gradient-to-br from-green-200 via-lime-300 to-green-400",
  summer: "bg-gradient-to-br from-yellow-200 via-orange-300 to-red-400",
  fall: "bg-gradient-to-br from-orange-100 via-amber-500 to-red-700",
  winter: "bg-gradient-to-br from-blue-100 via-sky-300 to-blue-500",
};

export default function SeasonalWrapper({ children }) {
  const season = useSeason();
  const theme = seasonalThemes[season] || "bg-white";

  return <div className={`min-h-screen ${theme} transition-all`}>{children}</div>;
}