
import React, { useState } from "react";
import useGirth from "../hooks/useGirth";
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer
} from "recharts";

export default function GirthTracker({ treeId = "black-pine" }) {
  const { logs, addLog } = useGirth(treeId);
  const [input, setInput] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!isNaN(parseFloat(input))) {
      addLog(input);
      setInput("");
    }
  };

  const formattedData = logs.map(log => ({
    name: new Date(log.timestamp.seconds * 1000).toLocaleDateString(),
    girth: log.value
  }));

  const latest = logs.length > 0 ? logs[logs.length - 1].value : 0;
  const isThick = latest >= 2;

  return (
    <div className="bg-black/60 border border-white/10 rounded-xl p-4 shadow-lg space-y-4">
      <h2 className="text-xl font-bold text-center"> Trunk Growth</h2>

      {isThick && <div className="text-green-400 text-center font-bold"> Trunk thick enough ( 2")</div>}

      <form onSubmit={handleSubmit} className="flex gap-2 items-center justify-center">
        <input
          type="number"
          step="0.01"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Enter girth in inches"
          className="bg-zinc-800 p-2 rounded border border-zinc-700 text-white w-32"
        />
        <button type="submit" className="bg-green-700 hover:bg-green-600 text-white px-4 py-2 rounded">
          Add
        </button>
      </form>

      {logs.length > 0 ? (
        <ResponsiveContainer width="100%" height={200}>
          <LineChart data={formattedData}>
            <CartesianGrid strokeDasharray="3 3" stroke="#666" />
            <XAxis dataKey="name" stroke="#ccc" />
            <YAxis domain={[0, 3]} stroke="#ccc" />
            <Tooltip />
            <Line type="monotone" dataKey="girth" stroke="#00ff99" strokeWidth={2} dot />
          </LineChart>
        </ResponsiveContainer>
      ) : (
        <p className="text-center text-sm text-gray-400">No data yet. Add your first log!</p>
      )}
    </div>
  );
}
