import React, { useEffect, useState } from 'react';

const API_KEY = "6585880ac4cd8619d66a2237d2341623"; // User's real API key
const ZONE_COORDS = { lat: 38.8, lon: -89.9 }; // Approx center for USDA Zone 67

export default function WeatherAlerts() {
  const [alert, setAlert] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    async function fetchAlerts() {
      try {
        const res = await fetch(
          `https://api.openweathermap.org/data/2.5/weather?lat=${ZONE_COORDS.lat}&lon=${ZONE_COORDS.lon}&appid=${API_KEY}&units=imperial`
        );
        const data = await res.json();

        if (data?.main?.temp !== undefined) {
          const temp = data.main.temp;
          const wind = data.wind?.speed || 0;
          const alertMessages = [];

          if (temp > 95) alertMessages.push(" Heat alert: Shade your bonsai!");
          if (temp < 35) alertMessages.push(" Frost alert: Protect your roots!");
          if (wind > 25) alertMessages.push(" Wind alert: Secure those branches!");

          setAlert(alertMessages.length ? alertMessages.join(" ") : " No extreme weather currently.");
        } else {
          setAlert(" No usable weather data received.");
        }
      } catch (err) {
        setError("Failed to fetch weather.");
        console.error(err);
      }
    }

    fetchAlerts();
  }, []);

  return (
    <div className="mt-6 p-4 border border-yellow-500 bg-zinc-800 rounded">
      <h2 className="text-xl font-bold mb-2 text-yellow-400"> Weather Alert</h2>
      {error && <p className="text-red-500">{error}</p>}
      {!error && <p>{alert || "Loading..."}</p>}
    </div>
  );
}
