
import React, { useEffect, useState } from "react";
import { getFirestore, doc, onSnapshot } from "firebase/firestore";
import { db } from "../firebase";


const moodThemes = {
  spooky: "bg-gradient-to-br from-black via-zinc-800 to-purple-900 border-purple-800",
  serene: "bg-gradient-to-br from-green-200 via-teal-100 to-blue-100 border-blue-200",
  chaotic: "bg-gradient-to-br from-red-800 via-black to-yellow-700 border-red-900",
  default: ""
};

export default function MoodWrapper({ treeId, children }) {
  const [mood, setMood] = useState("default");

  useEffect(() => {
    const ref = doc(db, "bonsai_mood", treeId);
    const unsub = onSnapshot(ref, (docSnap) => {
      if (docSnap.exists()) {
        setMood(docSnap.data().mood || "default");
      }
    });
    return () => unsub();
  }, [treeId]);

  return (
    <div className={`p-2 rounded-2xl border ${moodThemes[mood]}`}>
      {children}
    </div>
  );
}
