
import React from "react";
import useTasks from "../hooks/useTasks";

export default function TaskDashboard({ treeId = "black-pine" }) {
  const { tasks, skipped } = useTasks(treeId);

  return (
    <div className="bg-zinc-900 border border-zinc-700 rounded-xl p-4 shadow-md space-y-4">
      <h2 className="text-2xl font-bold text-white text-center mb-2"> Tasks for {treeId}</h2>

      <ul className="space-y-2">
        {tasks.map((task, i) => (
          <li key={i} className="bg-zinc-800 p-3 rounded shadow text-white border-l-4 border-green-500">
             {task.label}
          </li>
        ))}
      </ul>

      {skipped.length > 0 && (
        <div className="mt-6">
          <h3 className="text-lg text-yellow-300 font-bold"> Skipped Tasks (Trunk thick)</h3>
          <ul className="mt-2 space-y-2">
            {skipped.map((task, i) => (
              <li key={i} className="bg-black/40 text-yellow-400 p-3 rounded border border-yellow-800">
                 {task.label} <br />
                <span className="text-xs italic opacity-80">{task.reason}</span>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}
