import React from "react";
import TaskDashboard from "./components/TaskDashboard";
import GirthTracker from "./components/GirthTracker";
import PhotoGallery from "./components/PhotoGallery";
import TarotDraw from "./components/TarotDraw";
import WeatherAlerts from "./components/WeatherAlerts";
import MoodWrapper from "./components/MoodWrapper";
import SeasonalWrapper from "./components/SeasonalWrapper";

function App() {
  return (
    <SeasonalWrapper>
      <MoodWrapper>
        <div className="min-h-screen p-4 text-white">
          <h1 className="text-4xl font-bold mb-4 spooky-glow">
             Bonsai Tracker: Tim Burton Edition
          </h1>
          <WeatherAlerts />
          <TaskDashboard />
          <GirthTracker />
          <PhotoGallery />
          <TarotDraw />
        </div>
      </MoodWrapper>
    </SeasonalWrapper>
  );
}

export default App;
