
import { useEffect, useState } from "react";
import { getDocs, collection } from "firebase/firestore";
import { db } from "../firebase";
import { getTrunkStatus } from "./getTrunkStatus";
import tasksData from "../data/bonsai_tasks_recurring_enhanced.json";

export default function useTasks(treeId = "black-pine") {
  const [tasks, setTasks] = useState([]);
  const [skipped, setSkipped] = useState([]);

  useEffect(() => {
    async function loadTasks() {
      const status = await getTrunkStatus(treeId);
      const isThick = status.isThick;

      const excluded = [
        "heavy feeding",
        "unrestricted growth",
        "delay pruning",
        "maximize foliage"
      ];

      const result = [];
      const skippedList = [];

      for (let task of tasksData) {
        const appliesToTree = !task.treeIds || task.treeIds.includes(treeId);
        const isExcluded = isThick && excluded.includes(task.type?.toLowerCase());

        if (appliesToTree) {
          if (isExcluded) {
            skippedList.push({ ...task, reason: "Trunk thick enough ( 2\")" });
          } else {
            result.push(task);
          }
        }
      }

      setTasks(result);
      setSkipped(skippedList);
    }

    loadTasks();
  }, [treeId]);

  return { tasks, skipped };
}
