
import { getDocs, collection, query, orderBy, limit } from "firebase/firestore";
import { db } from "../firebase";

export async function getTrunkStatus(treeId) {
  const q = query(collection(db, "bonsai_girth", treeId, "logs"), orderBy("timestamp", "desc"), limit(1));
  const snap = await getDocs(q);
  if (snap.empty) return { isThick: false, latest: 0 };

  const latest = snap.docs[0].data().value;
  return {
    isThick: latest >= 2,
    latest
  };
}
