
import React, { useState, useEffect } from "react";
import { collection, addDoc, query, orderBy, getDocs } from "firebase/firestore";
import { db } from "../firebase";
import tarot from "../data/tarot_cards.json";

function getRandomCard() {
  const card = tarot[Math.floor(Math.random() * tarot.length)];
  const reversed = Math.random() < 0.5;
  return { ...card, reversed };
}

export default function TarotDraw({ treeId = "black-pine" }) {
  const [draw, setDraw] = useState(null);
  const [history, setHistory] = useState([]);
  const [note, setNote] = useState("");

  const handleDraw = async () => {
    const card = getRandomCard();
    setDraw(card);

    const entry = {
      card: card.name,
      reversed: card.reversed,
      meaning: card.reversed ? card.meaning_rev : card.meaning_up,
      timestamp: new Date(),
      note,
    };

    await addDoc(collection(db, "bonsai_tarot", treeId, "draws"), entry);
    setNote("");
    loadHistory();
  };

  const loadHistory = async () => {
    const q = query(collection(db, "bonsai_tarot", treeId, "draws"), orderBy("timestamp", "desc"));
    const snap = await getDocs(q);
    const entries = snap.docs.map((doc) => doc.data());
    setHistory(entries);
  };

  useEffect(() => {
    loadHistory();
  }, [treeId]);

  return (
    <div className="bg-zinc-900 p-4 rounded-xl border border-white/10 space-y-4 shadow">
      <h2 className="text-xl font-bold text-purple-300"> Tarot Draw</h2>

      <textarea
        className="w-full bg-zinc-800 p-2 rounded text-white border border-zinc-700"
        placeholder="Add a note or intention..."
        value={note}
        onChange={(e) => setNote(e.target.value)}
      />

      <button
        onClick={handleDraw}
        className="bg-purple-700 hover:bg-purple-600 text-white px-4 py-2 rounded shadow w-full"
      >
        Draw a Card
      </button>

      {draw && (
        <div className="bg-black/40 p-3 rounded text-purple-200 border border-purple-800">
          <h3 className="font-bold">{draw.name} {draw.reversed && "(Reversed)"}</h3>
          <p className="italic text-sm">{draw.reversed ? draw.meaning_rev : draw.meaning_up}</p>
        </div>
      )}

      {history.length > 0 && (
        <div className="pt-4">
          <h3 className="text-white font-bold mb-2"> Past Draws</h3>
          <ul className="space-y-2 max-h-48 overflow-y-auto">
            {history.map((h, i) => (
              <li key={i} className="bg-zinc-800 p-2 rounded text-sm border border-zinc-700">
                <strong>{h.card}</strong> {h.reversed && "(Reversed)"} {" "}
                <span className="italic">{new Date(h.timestamp.seconds * 1000).toLocaleString()}</span>
                {h.note && <div className="mt-1 text-zinc-400 italic">{h.note}</div>}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}
