
import React, { useState, useEffect } from "react";
import { ref, uploadBytes, listAll, getDownloadURL } from "firebase/storage";
import { storage } from "../firebase";

function PhotoGallery({ treeId }) {
  const [images, setImages] = useState([]);
  const [uploading, setUploading] = useState(false);

  useEffect(() => {
    const listImages = async () => {
      const folderRef = ref(storage, `bonsai_photos/${treeId}`);
      const result = await listAll(folderRef);
      const urls = await Promise.all(result.items.map(item => getDownloadURL(item)));
      setImages(urls);
    };
    listImages();
  }, [treeId]);

  const handleUpload = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
    setUploading(true);
    const imageRef = ref(storage, `bonsai_photos/${treeId}/${Date.now()}_${file.name}`);
    await uploadBytes(imageRef, file);
    const url = await getDownloadURL(imageRef);
    setImages(prev => [url, ...prev]);
    setUploading(false);
  };

  return (
    <div className="bg-slate-950 rounded-xl p-4 border border-slate-700 mt-4">
      <h2 className="text-lg font-bold mb-2"> {treeId} Photo Gallery</h2>
      <input type="file" onChange={handleUpload} className="mb-3" />
      {uploading && <p className="text-sm text-slate-400">Uploading...</p>}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
        {images.map((url, idx) => (
          <img
            key={idx}
            src={url}
            alt={`bonsai-${treeId}-${idx}`}
            className="rounded-lg border border-slate-800 shadow-md hover:scale-105 hover:shadow-lg transition-all"
          />
        ))}
      </div>
    </div>
  );
}

export default PhotoGallery;
